export const areCitaInputsValid = ({nombre, apellido, email, numeroTelefonico}) => {
    let errs = {}
    

    if (nombre.trim().length === 0)  errs.nombre = 'Escribe un nombre vlido'
    if (apellido.trim().length === 0)  errs.apellido = 'Escribe un apellido vlido'
    if (email.trim().length === 0)  errs.email = 'Escribe un email vlido'
    if (!/\S+@\S+\.\S+/.test(email))  errs.email = 'El correo es invlido'
    if (numeroTelefonico.trim().length === 0)  errs.numeroTelefonico = 'Escribe un nombre vlido'

    return errs
}