export const arePacienteInputsValid = ({nombre, apellido, email, numeroTelefonico, cedula}) => {
    let errs = {}
    

    if (nombre.trim().length === 0)  errs.nombre = 'Escribe un nombre vlido'
    if (cedula.trim().length === 0)  errs.cedula = 'Escribe una cdula vlida'
    if (apellido.trim().length === 0)  errs.apellido = 'Escribe un nombre vlido'
    if (email.trim().length === 0)  errs.email = 'Escribe un nombre vlido'
    if (!/\S+@\S+\.\S+/.test(email))  errs.email = 'El correo es invlido'
    if (numeroTelefonico.trim().length === 0)  errs.numeroTelefonico = 'Escribe un nombre vlido'

    return errs
}